body {
  font-family: Arial, sans-serif;
  background-color: #202123;
  color: #e0e0e0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.top-bar {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: 10px;
  z-index: 1000;
  width: 100%;
  max-width: 1200px;
  padding: 0 20px;
  box-sizing: border-box;
}

.top-bar button {
  background: #007aff;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

.top-bar button:hover {
  background: #005ecf;
}

.search-bar {
  flex: 1;
  display: flex;
  justify-content: center;
  gap: 10px;
}

.search-bar input {
  padding: 8px;
  background: #3c3f41;
  border: 1px solid #5a5d60;
  color: #e0e0e0;
  border-radius: 4px;
  width: 100%;
  max-width: 400px;
}

.search-bar button {
  background: #007aff;
  color: white;
  padding: 8px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.search-error {
  position: fixed;
  top: 70px;
  left: 50%;
  transform: translateX(-50%);
  color: #ff6b6b;
  font-size: 14px;
  z-index: 1000;
}

.task-groups {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  overflow-x: auto;
  gap: 20px;
  padding: 100px 20px 20px 20px;
  scroll-behavior: smooth;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
  padding-right: 100px;
}

.task-group {
  background: #2d2f32;
  padding: 15px;
  border-radius: 8px;
  min-width: 350px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  gap: 10px;
}

.task-item textarea,
.task-input textarea {
  flex: 1;
  padding: 8px;
  background: #3c3f41;
  border: 1px solid #5a5d60;
  color: #e0e0e0;
  border-radius: 4px;
  resize: none;
  overflow-y: auto;
  height: 120px; /* Ta sama wysokość */
  width: 280px; /* Ta sama szerokość */
  box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.3),
    inset -2px -2px 5px rgba(255, 255, 255, 0.1);
}

.task-item textarea {
  cursor: pointer; /* Kursor wskazujący, że można kliknąć */
}

.task-item textarea.completed {
  text-decoration: line-through;
  opacity: 0.7;
}

.task-item button {
  margin-left: 10px;
  padding: 8px;
  background: #ff6b6b;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.task-input {
  display: flex;
  align-items: flex-start;
  margin-top: 10px;
}

.task-input textarea {
  cursor: text; /* Kursor tekstowy dla pola do wpisywania */
}

.task-input button {
  margin-left: 10px;
  padding: 8px;
  background: #007aff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.add-category {
  background: #007aff;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 20px auto;
  display: block;
}

.add-category:hover {
  background: #005ecf;
}

/* Responsywność */
@media (max-width: 768px) {
  .top-bar {
    flex-direction: column;
    gap: 10px;
    padding: 10px;
  }

  .search-bar input {
    max-width: 100%;
  }

  .task-groups {
    padding-top: 150px;
  }
}
